.backdrop {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 10;
    background: rgba(0, 0, 0, 0.75);
}




.form {
    position: fixed;
    top: 30vh;
    left: 10%;
    width: 80%;
    z-index: 100;
    overflow: hidden;

    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-around;
    /* align-items: center; */
    background-color: #7e0080;
    border-radius: 5px;
    color: white;
    padding: 1rem;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 1) 0px 30px 60px -30px, rgba(10, 37, 64, 0.5) 0px -2px 6px 0px inset;
}



.form input {
    border-radius: 4px;
    border-color: 00ff5aab;
    font-size: x-large;
    padding: 5px;
}

.form label {
    font-size: x-large;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

.form h2 {
    margin: auto;
    padding: 5px;
    text-align: center;
}

.titleInput {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.titleInput input {
    margin-left: 1em;
    width: 100%
}


.timeInput {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    align-items: center;
    margin: 1rem;
}

.timeInput input {
    margin-left: 1em;
    width: 3rem;
}

.buttonGroup {
    display: flex;
    flex-wrap: wrap;
}